body,
html,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
}

header,
nav,
footer {
  background-color: #fdc45a;
  width: 100%;
}

a {
  color: black;
  font-weight: bold;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  height: 35px;
  margin: 20px;
}

.ProjectContainer {
  border-style: solid;
  border-width: thin;
  border-radius: 10px;
  width: 220px;
  margin: 15px;
}

.NameContainer,
.InfoContainer {
  margin: 10px;
}

.ToolsLangsContainer {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.ToolLang {
  margin: 0px 5px 0px 5px;
  border-radius: 5px;
  background: #4f8b93;
  padding: 3px;
  color: white;
}

.VertLine {
  border-left: 2px solid black;
}

.ProjectsContainer,
.MainContainer {
  margin-top: 30px;
  margin-bottom: 30px;
}

.CenterContent {
  display: flex;
  flex-flow: column;
  height: 100%;
  text-align: center;
}

.MainContainer {
  flex: 1 1 auto;
}

.ProjectsContainer {
  flex: 1 1 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1000px;
  align-self: center;
}

.ContactContainer {
  flex: 1 1 auto;
  display: flex;
  flex-flow: column;
  padding-top: 80px;
}

.GameContainer {
  display: flex;
  flex: 1 1 auto;
  justify-content: center;
  flex-flow: column;
}

header,
nav,
footer {
  flex: 0 1 auto;
  display: flex;
  justify-content: center;
}

header {
  align-items: center;
}

nav {
  padding-bottom: 10px;
  box-shadow: 0 8px 10px -5px darkgray;
}

nav a,
.ContactContainer div {
  margin: 10px;
}

footer {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: auto;
  box-shadow: 0 -2px 15px darkgray;
  flex-flow: column;
}
